<template>
	<div class="page-content-tabbar tabbar">
		<div class="tabbar-item" @click="changeTabbar('Menu')">
			<div class="icon-wrapper">
				<i class="icon ion-menu"></i>
			</div>
			<div class="text-wrapper">
				<span>导航</span>
			</div>
		</div>
		
		<div class="tabbar-item" :class="{'tabbar-item-active': returnRes.activeType == 'Server'}" @click="changeTabbar('Server')">
			<div class="icon-wrapper">
				<i class="icon ion-server"></i>
			</div>
			<div class="text-wrapper">
				<span>服务案例</span>
			</div>
		</div>

		<div class="tabbar-item" :class="{'tabbar-item-active': returnRes.activeType == 'About'}" @click="changeTabbar('About')">
			<div class="icon-wrapper">
				<i class="icon ion-about"></i>
			</div>
			<div class="text-wrapper">
				<span>关于我们</span>
			</div>
		</div>

		<div class="tabbar-item" :class="{'tabbar-item-active': returnRes.activeType == 'Consult'}" @click="changeTabbar('Consult')">
			<div class="icon-wrapper">
				<i class="icon ion-consult"></i>
			</div>
			<div class="text-wrapper">
				<span>咨询</span>
			</div>
		</div>
	</div>
</template>

<script>
	import _ from 'lodash'

	export default {
		name: 'Tabbar',
		data() {
			return {
				returnRes: {
					activeType: ''
				}
			}
		},
		mounted() {
			if (sessionStorage.activeType) {
				this.returnRes.activeType = sessionStorage.activeType;
			}
		},
		methods: {
			changeTabbar(name) {
				this.returnRes.activeType = name;

				if (name != 'Menu') {
					this.$router.push({ name: _.lowerCase(name), path: _.lowerCase(name) });
				}

				this.$emit('returnMenuRes', this.returnRes);
			}
		}
	}
</script>

