<template>
	<div id="nav">
		<div class="menu-mask" :class="{'no': show}"></div>
		<div class="nav" :class="{'nav-show': show}" @click="closeMenu">
			<div class="menu">
				<div class="admin-aside-nemu-logo">
					<img src="./../assets/img/logo/logo_menu.png">
				</div>

				<div class="menu-wapper">
					<ul class="list-taipu">
						<li @click="changeMenu('Home')">首页</li>
					</ul>

					<ul class="list-taipu">
						<li @click="changeMenu('Experience')">精密体验</li>
						<li @click="changeMenu('Purify')">血液净化</li>
						<li @click="changeMenu('Antifading')">干细胞抗衰</li>
					</ul>

					<ul class="list-taipu">
						<li @click="changeMenu('Hospital')">合作医院</li>
						<li @click="changeMenu('Team')">专家团队</li>
						<li @click="changeMenu('Server')">服务案例</li>
						<li @click="changeMenu('About')">关于泰璞健康</li>
						<li @click="changeMenu('Consult')">联系我们</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import _ from 'lodash'

	export default {
		name: 'Menu',
		props: ['show'],
		data() {
			return {
				returnRes: {}
			}
		},
		mounted() {
			
		},
		methods: {
			changeMenu(name) {
				this.returnRes.activeType = name;
				this.returnRes.showMenu = false;
				
				if (name == 'Consult') sessionStorage.from = 'Menu';

				console.log(_.lowerCase(name));

				this.$router.push({ name: _.lowerCase(name), path: _.lowerCase(name) });
				this.$emit('returnMenuRes', this.returnRes);
			},
			closeMenu() {
				this.returnRes.showMenu = false;
				this.$emit('returnMenuRes', this.returnRes);
			}
		}
	}
</script>

