</template>

<script>
	export default {
		props: ['show'],
		data() {
			return {
				showTel: ''
			}
		},
		mounted() {
		},
		methods: {
			closeAlert() {
				this.showTel = false;
				this.$emit('returnRes', { showTel: false })
			},
			alertComfrim() {
				this.showTel = false;
				this.$emit('returnRes', { showTel: false })
			}
		},
		watch: {
			'show': {
				handler(newValue, oldValue) {
					this.showTel = newValue;
				}
			}
		}
	}
</script>

