import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  linkActiveClass: 'active',
  linkExactActiveClass: 'active',
  routes: [
    { path: '/', name: 'home', component: Home, meta: { title: '泰璞健康' } },
    { path: '/about', name: 'about', component: () => import('./views/About.vue'), meta: { title: '关于我们' } },
    { path: '/server', name: 'server', component: () => import('./views/Server.vue'), meta: { title: '服务案例' } },
    { path: '/hospital', name: 'hospital', component: () => import('./views/Hospital.vue'), meta: { title: '合作医院' } },
    { path: '/team', name: 'team', component: () => import('./views/Team.vue'), meta: { title: '专家团队' } },
    { path: '/consult', name: 'consult', component: () => import('./views/Consult.vue'), meta: { title: '咨询' } },
    { path: '/experience', name: 'experience', component: () => import('./views/Experience.vue') },
    { path: '/purify', name: 'purify', component: () => import('./views/Purify.vue') },
    { path: '/antifading', name: 'antifading', component: () => import('./views/Antifading.vue') },
    { path: '/hospital-detail', name: 'hospital-detail', component: () => import('./views/HospitalDetail.vue') },
    { path: '/team-detail', name: 'team-detail', component: () => import('./views/TeamDetail.vue') },
    { path: '/server-detail', name: 'server-detail', component: () => import('./views/ServerDetail.vue') },
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
});

export default router
